import React, { useEffect } from 'react';
import { Switch, Route, 
  Redirect, withRouter } from 'react-router-dom';
import Aos from 'aos';

import Header from './HeaderComponent';
import Footer from './FooterComponent';
import Home from './HomeComponent';
import Resume from './ResumeComponent';
import Contact from './ContactComponent';
import ProjectView from './project_components/ProjectComponent';
import WebDev from './project_components/WebComponent';
import DeskApps from './project_components/DeskComponent';
import DataSci from './project_components/DataSciComponent';

import { CATEGORIES } from '../shared/categories';
import { PROJECTS } from '../shared/projects';

const Main = () => {
  useEffect(() => {
    Aos.init({ duration: 900 })
  }, []);

  const categories = CATEGORIES;

  const projects = PROJECTS;
  const featuredProjects = projects.filter((project) => project.featured)

  const WebDevPage = () => {
    return (
      <WebDev 
        projects={projects.filter((project) => project.category === "web-dev")} 
      />
    );
  };

  const DeskAppPage = () => {
    return (
      <DeskApps 
        projects={projects.filter((project) => project.category === "desktop-dev")} 
      />
    );
  }; 

  const DataSciPage = () => {
    return (
      <DataSci 
        projects={projects.filter((project) => project.category === "data-science")} 
      />
    );
  }; 

  const ProjWithId = ({match}) => {
    return(
      <ProjectView 
        project={projects.filter((project) => project.id === parseInt(match.params.projId, 10))[0]}
      />
    );
  };

  return (
    <div>
      <Header />

      <Switch>
        <Route path="/home" component={() => <Home categories={categories} featuredProjects={featuredProjects} />} />
        <Route path="/resume" component={Resume} />
        <Route path="/contact" component={Contact} />
        <Route path="/webdev" component={WebDevPage} />
        <Route path="/desktop" component={DeskAppPage} />
        <Route path="/datascience" component={DataSciPage} />
        <Route path="/projects/:projId" component={ProjWithId} />
        <Redirect to="/home" />
      </Switch>

      <Footer />
    </div>
  );
};

export default withRouter(Main);