import React from 'react';
import {
  Card, CardText, CardBody,
  CardHeader, CardSubtitle,
  List, ListInlineItem,
  Button
} from 'reactstrap';
import SideNav from '../SideNavComponent';
import GitHubIcon from '@material-ui/icons/GitHub';
import LaunchIcon from '@material-ui/icons/Launch';
import Carousel from 'react-grid-carousel'

const PUBLIC_URL = process.env.PUBLIC_URL;

const ProjectView = (props) => {
  const project = props.project;

  const tools = project.tools.map((tool, _id) => {
    return (
      <ListInlineItem key={_id} tag="p" className="mr-4">
        {tool}
      </ListInlineItem>
    );
  });

  // Cook up buttons for the available links
  const buttonVisit = (
    project.visit_link
    ?
    <Button style={{ width: "25ch" }}>
      <a className="text-reset text-decoration-none" href={project.visit_link} rel="noreferrer" target="_blank"><strong>Try it!</strong></a>
      <LaunchIcon className="ml-3" />
    </Button> 
    : 
    null
  );
  const buttonGit = (
    project.git_link
    ?
    <Button style={{ width: "25ch" }}>
      <a className="text-reset text-decoration-none" href={project.git_link} rel="noreferrer" target="_blank"><strong>Github Repo</strong></a>
      <GitHubIcon className="ml-3" />
    </Button> 
    : 
    null
  );
  const buttonInfo = (
    project.info_link
    ?
    <Button style={{ width: "25ch" }}>
      <a className="text-reset text-decoration-none" href={project.info_link} rel="noreferrer" target="_blank"><strong>More Info</strong></a>
      <LaunchIcon className="ml-3" />
    </Button> 
    : 
    null
  );

  return (
    <main className="container">
      <SideNav />
      
      <div className="row justify-content-center" data-aos="zoom-in">
        <Card className="info-card text-center border-0">
          <CardHeader tag="h1">{project.title}</CardHeader>
          <CardBody>
            <CardSubtitle tag="h2" className="mb-2 text-muted" style={{ fontStyle: "italic" }}>{project.brief}</CardSubtitle>

            <hr/>

            <div className="info-carousel-frame d-flex justify-content-center">
              <div className="info-carousel" style={{ width: "75%" }}>
                <Carousel cols={1} rows={1} gap={10} loop>
                  {
                    project.slides.map((slide, idx) => {
                      return (
                        <Carousel.Item>
                          <img src={`${PUBLIC_URL}/${slide}`} alt={`slide-${idx}`} width="100%" />
                        </Carousel.Item>
                      );
                    })
                  }
                </Carousel>
              </div>
            </div>

            <hr/>

            <CardText>{project.purpose}</CardText>

            <hr/>

            <div className="text-left ml-sm-4">
              <h3>Technologies Used:</h3>
              <List type="inline" className="ml-4">
                {tools}
              </List>
            </div>

            <hr/>
            
            {
              (project.visit_link && project.git_link)
              ?
              <div className="row">
                <div className="col-12 col-sm-6 my-2">
                  { buttonVisit }
                </div>
                
                <div className="col-12 col-sm-6 my-2">
                  { buttonGit }
                </div>
              </div>
              :
              <> { buttonVisit } {buttonGit} {buttonInfo} </>
            }
          </CardBody>
        </Card>
      </div>
    </main>
  );
};

export default ProjectView;