import { blue } from "@material-ui/core/colors";
import { AutoRotatingCarousel, Slide } from "material-auto-rotating-carousel";

const PUBLIC_URL = process.env.PUBLIC_URL;

const AutoRotatingCarouselModal = ({ state, setState, slides }) => {
  const isMobile = (window.innerWidth < 768);

  return (
    <>
      <AutoRotatingCarousel
        label="Back"
        open={state.showCarousel}
        onClose={(e) => {
          e.preventDefault(); 
          setState({ showCarousel: false })
        }}
        onStart={() => setState({ showCarousel: false })}
        autoplay={false}
        mobile={isMobile}
        style={{ position: "absolute" }}
      >

        {
          slides.map((slide, idx) => 
            <Slide
              media={
                <img src={`${PUBLIC_URL}/${slide}`} alt={`slide-${idx}`} width="100%" height="100%" />
              }
              mediaBackgroundStyle={{ backgroundColor: blue, height: "85%" }}
              style={{ backgroundColor: "rgba(255, 255, 255)" }}
            /> 
          )
        }

      </AutoRotatingCarousel>
    </>
  );
};

export default AutoRotatingCarouselModal;