import React from 'react';
import { Container, Table } from 'reactstrap';

import SideNav from './SideNavComponent';

import { GITHUB, LINKEDIN, EMAIL, PHONE } from '../shared/refLinks';

const Contact = () => {
  return (
    <Container>
      <SideNav />
      <Table size="" className="w-50 offset-1 table-hover" data-aos="fade-right">
        <tbody>
          <tr>
            <td className="col-2 w-25">Email Me:<br/><i className="fa fa-envelope-o fa-2x"></i></td>
            <td className="text-center align-middle"><a href={"mailto:" + EMAIL}>{EMAIL}</a></td>
          </tr>
          <tr>
            <td className="col-2 w-25">Call Me:<br/><i className="fa fa-phone fa-2x"></i></td>
            <td className="text-center align-middle"><a href={"tel:" + PHONE}>{PHONE}</a></td>
          </tr>
          <tr>
            <td className="col-2 w-25">LinkedIn:<br/><i className="fa fa-linkedin fa-2x"></i></td>
            <td className="text-center align-middle"><a href={LINKEDIN} rel="noreferrer" target="_blank">{LINKEDIN.slice(12)}</a></td>
          </tr>
          <tr>
            <td className="col-2 w-25">GitHub:<br/><i className="fa fa-github fa-2x"></i></td>
            <td className="text-center align-middle"><a href={GITHUB} rel="noreferrer" target="_blank">{GITHUB.slice(8)}</a></td>
          </tr>
          <tr>
            <td></td>
            <td></td>
          </tr>
        </tbody>
      </Table>
    </Container>
  );
};

export default Contact;