import React from 'react';
import { Link } from 'react-router-dom';

import { GITHUB } from '../shared/refLinks';

const SideNav = () => {
  return (
    <div className="sidenav d-none d-lg-block pl-3" data-aos="zoom-in" data-aos-delay="3000">
      <div className="row">
        <Link to="/resume" className="btn"><i className="fa fa-id-badge fa-4x"></i><br/><span>Resume</span></Link>
        <a className="btn" href={GITHUB} rel="noreferrer" target="_blank"><i className="fa fa-github fa-4x"></i><br/><span>GitHub</span></a>
      </div>
  </div>
  );
};

export default SideNav;