import React, { useState } from 'react';
import { Row, Button, ButtonGroup } from 'reactstrap';
import { AiOutlineFilePdf, AiOutlineFileWord } from "react-icons/ai";
import { Tooltip } from 'reactstrap';
import Carousel from 'react-grid-carousel'

import SideNav from './SideNavComponent';

const PUBLIC_URL = process.env.PUBLIC_URL;

const TooltipItem = props => {
  const { items, id } = props;

  const [tooltipOpen, setTooltipOpen] = useState(false);
  const toggle = () => setTooltipOpen(!tooltipOpen);

  return (
    <>
      <Button className="mx-4" color="bg-light" id={"Tooltip-" + id}>
        <a href={ items.href } download={ items.fileName }>{items.icon}</a>
        <p className="d-lg-none m-0">{ items.tip }</p>
      </Button>
      
      <Tooltip
        placement={items.placement}
        isOpen={tooltipOpen}
        target={"Tooltip-" + id}
        toggle={toggle}
      >
        {items.tip}
      </Tooltip>
    </>
  );
};

const Resume = () => {
  return (
    <>
      <SideNav />

      <div className="container resume-carousel" data-aos="fade-up">
        <Carousel cols={1} rows={1} gap={10} loop>
          <Carousel.Item>
            <img width="100%" src={`${PUBLIC_URL}/assets/docs/resume-1.jpg`} alt="resume-1" />
          </Carousel.Item>
          <Carousel.Item>
            <img width="100%" src={`${PUBLIC_URL}/assets/docs/resume-2.jpg`} alt="resume-2" />
          </Carousel.Item>
        </Carousel>
      </div>

      <Row className="justify-content-center mt-3">
        <ButtonGroup>
          {[
            { placement: "left", icon: <AiOutlineFilePdf color="#CC0202" size="30" />, tip: "Download PDF File", href:"assets/docs/resume.pdf", fileName: "TshepoCV.pdf" },
            { placement: "right", icon: <AiOutlineFileWord color="#3616D9" size="30" />, tip: "Download Word File", href: "assets/docs/resume.docx", fileName: "TshepoCV.docx" }
          ].map((tooltip, i) => {
            return <TooltipItem key={i} items={tooltip} id={i} />;
          })}
        </ButtonGroup>
      </Row>
    </>
  );
};

export default Resume;