import React from 'react';
import { Link } from 'react-router-dom';

import { GITHUB, LINKEDIN, EMAIL } from '../shared/refLinks';

const Footer = () => {
  return (
    <div className="footer py-3 mt-4" style={{ backgroundColor: "#e9ecef" }}>
      <div className="container">
        <div className="row">
          <div className="col-12 col-sm-6">
            <ul className="list-unstyled list-inline text-center">
              <li className="list-inline-item"><Link className="text-decoration-none mr-3" to="/home">Portfolio</Link></li>
              <li className="list-inline-item"><Link className="text-decoration-none mr-3" to="/resume">Resume</Link></li>
              <li className="list-inline-item"><Link className="text-decoration-none" to="/contact">Get in touch </Link></li>
            </ul>
          </div>

          <div className="col-12 col-sm">
            <div className="text-center">
              <a className="btn btn-social-icon btn-github mx-2" href={GITHUB} rel="noreferrer" target="_blank"><i className="fa fa-github"></i></a>
              <a className="btn btn-social-icon btn-linkedin mx-2" href={LINKEDIN} rel="noreferrer" target="_blank"><i className="fa fa-linkedin"></i></a>
              <a className="btn btn-social-icon mx-1" href={"mailto:" + EMAIL}><i className="fa fa-envelope-o"></i></a>
            </div>
          </div>
        </div>
        
        <div className="row justify-content-center mt-3 mt-sm-1">
          <div className="col-auto">
            <p>Designed by Tshepo Mosoeunyane</p>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Footer;