import React from 'react';
import { Link } from 'react-router-dom';
import ArrowForwardIcon from '@material-ui/icons/ArrowForward';

import SideNav from './SideNavComponent';
import RenderProjectCard from './ProjectCard';

import '../css/category-card.css';

const PUBLIC_URL = process.env.PUBLIC_URL;

const isMobile = (window.innerWidth < 768);

const RenderCategoryCard = ({ category }) => {
  const CardContent = () => {
    const cardImage = isMobile ? category.imageMobile : category.imageDesktop;
    return (
      <div key={category.id} className="category-card-frame" data-aos="zoom-in">
        <div className="category-card" style={{background: `url(${PUBLIC_URL}/${cardImage})`, backgroundSize: "cover"}}>
          <h1>{category.title}</h1>
        
          <Link to={category.link} className={`poster button-${category.buttonStyle}`}>
            <div className="poster-icon"><ArrowForwardIcon fontSize="large" /></div>
            <span className="d-md-none">Click me</span>
          </Link>
        </div>
      </div>
    );
  };

  return (
    <>
      {
        isMobile
        ?
        // For mobile: only the buttons are links
        <div>
          <CardContent />
        </div>
        :
        // For desktop: whole frame is a link
        <Link className="text-decoration-none" to={category.link}>
          <CardContent />
        </Link>
      }
    </>
  );
};

const Home = (props) => {
  const categoryCards = props.categories.map((category, idx) => {
    const numButtonStyles = 4;    // number of button styles available in css (check category-card.css)
    category.buttonStyle = idx % numButtonStyles;

    return (
      <RenderCategoryCard category={category} />
    );
  });

  const projectCards = props.featuredProjects.map((project) => {
    return (
      <RenderProjectCard
        project={project} 
      />
    );
  });

  return (
    <main className="container">

      <SideNav />

      <h1 data-aos="zoom-in" data-aos-offset="10">Project Categories</h1>

      <div className="row justify-content-around">
        {categoryCards}
      </div>

      <h1 className="mt-5" data-aos="zoom-in" data-aos-offset="10">Some of My Projects</h1>

      <div className="row justify-content-around">
        {projectCards}
      </div>

    </main>
  );
};

export default Home;