import { useState } from 'react';
import { Link } from 'react-router-dom';
import {Button} from "@material-ui/core";

import AutoRotatingCarouselModal from './CarouselModal';

import '../css/project-card.css';

const PUBLIC_URL = process.env.PUBLIC_URL;

const isMobile = (window.innerWidth < 768);

const RenderProjectCard = ({ project }) => {
  const [state, setState] = useState({ showCarousel: false });

  const CardContent = () => {
    const previewClick = (e) => {
      e.preventDefault();
      setState({ showCarousel: true });
    };

    return (
      <>
        <AutoRotatingCarouselModal
          state={state}
          setState={setState}
          slides={project.slides}
        />

        <div className="details">
          <div className="content">
            <h1>{project.title}</h1>
            <p>{project.brief}</p>
          </div>

          <div className="buttons">
            <Button className="d-none d-md-block" size="small" onClick={(e) => previewClick(e)}>
              Preview
            </Button>

            <Button size="small">
              <Link to={project.link}>
                More Info
              </Link>
            </Button>
          </div>
        </div>
      </>
    );
  };

  const imageCover = `url('${PUBLIC_URL}/${project.cover}'`;

  return (
    <>
      {
        isMobile
        ?
        // For mobile: only the buttons are links
        <div key={project.id} data-aos="fade-up" className="project-card" style={{ background: imageCover, backgroundSize: "cover", backgroundPosition: "center" }}>
          <CardContent />
        </div>
        :
        // For desktop: whole frame is a link
        <div key={project.id} className="project-card-frame" data-aos="fade-up">
          <Link to={project.link} className="project-card text-decoration-none" style={{ background: imageCover, backgroundSize: "cover", backgroundPosition: "center" }}>
            <CardContent />
          </Link>
        </div>
      }
    </>
  );
};

export default RenderProjectCard;