import React, { useState, useEffect, useRef } from 'react';
import {
  Collapse, Navbar,
  Nav, NavItem, Jumbotron, Button
} from 'reactstrap';

import { NavLink, Link } from 'react-router-dom';
import { GITHUB } from '../shared/refLinks';

const PUBLIC_URL = process.env.PUBLIC_URL;

const isMobile = (window.innerWidth < 768);

const Header = () => {
  const [state, setState] = useState({ openNav: false, showText: !isMobile });

  const toggleNav = () => setState({ openNav: !state.openNav, showText: state.showText });

  const toggleText = () => setState({ showText: !state.showText });

  // Close navbar collpse when user clicks outside the navbar
  const navRef = useRef();

  const handleClickOutside = e => {
    if (!navRef.current.contains(e.target)) {
      setState({ openNav: false, showText: state.showText });
    }
  };

  useEffect(() => {
    document.addEventListener('mousedown', handleClickOutside);
    return () => document.removeEventListener('mousedown', handleClickOutside);
  });

  const mobileJumboHero = `linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.6)), url('${PUBLIC_URL}/assets/images/skies-mobile.jpg')`;
  const desktopJumboHero = `linear-gradient(rgba(0, 0, 0, 0.0), rgba(0, 0, 0, 0.7)), url('${PUBLIC_URL}/assets/images/skies-desktop.jpg')`;
  const jumboImage = isMobile ? mobileJumboHero : desktopJumboHero;

  return (
    <>
      <Navbar color="light" light expand="md" className="fixed-top py-0">
        <div className="container-fluid mx-0 mx-md-5" ref={navRef}>
          <Link to="/" className="navbar-brand text-reset text-decoration-none py-1">TM</Link>

          <button class="navbar-toggler border-0" type="button" onClick={toggleNav} aria-label="Toggle navigation">
            {
              state.openNav
              ?
              <span className="fa fa-times" style={{color: "rgba(255, 250, 250, 0.8)"}}></span>
              :
              <span className="navbar-toggler-icon"></span>
            }
          </button>

          <Collapse isOpen={state.openNav} navbar>
            <Nav className="d-flex justify-items-center align-items-center ml-auto" navbar>
              <NavItem className="mx-3 my-1">
                <NavLink to="/home">Home</NavLink>
              </NavItem>
              <NavItem className="mx-3 my-1">
                <NavLink to="/resume">Resume</NavLink>
              </NavItem>
              <NavItem className="mx-3 my-1">
                <NavLink to="/contact">Get in touch</NavLink>
              </NavItem>
              <NavItem className="mx-3 my-1">
                <a href={GITHUB} rel="noreferrer" target="_blank">GitHub</a>
              </NavItem>
            </Nav>
          </Collapse>
        </div>
      </Navbar>

      <Jumbotron className="mt-5 mb-0 mb-lg-5 d-flex" style={{ backgroundImage: jumboImage, backgroundSize: "cover" }}>
        <div className="container pt-3 px-1 px-xl-5 mt-auto">
          <div className="content">
            <h1>Tshepo Mosoeunyane</h1>

            <div className="self-intro px-1 px-sm-2 px-xl-3 pt-1">
              { 
                state.showText && 
                <>
                  <p>From empathizing with a client and defining the problem to delivering a full <span className="intro-highlight">Business Solution</span>, I dedicate my efforts in assisting communities.</p>
                  <p>Coming from an Aerospace Engineering background, providing effective <span className="intro-highlight">Business Solutions</span> is my calling.</p>
                </>
              }
              <p>With my tech tool-box in Software Development and Data Science, <br/>You and I can collaborate to build a <strong className="intro-highlight">Brighter Future!</strong></p>
            </div>
          </div>

          <div className="col-12 d-flex d-md-none justify-content-center">
            <Button className="px-0 py-1" onClick={toggleText}>
              { state.showText ? <span>Show less</span> : <span>Read more</span> }
            </Button>
          </div>
        </div>
      </Jumbotron>

      <div className="row mobile-nav d-lg-none d-flex justify-content-around mx-2 mx-sm-5 px-sm-5 my-4" style={{ border: "2px solid #fe6b8b", borderRadius: "25px", backgroundColor: "rgba(255, 142, 83, 0.2) " }}>
        <div className="text-md-left text-center">
          <Link className="btn" to="/resume"><i className="fa fa-id-badge fa-2x"></i><br/><span>Resume</span></Link>
        </div>
        <div className="text-md-left text-center">
          <a className="btn" href={GITHUB} rel="noreferrer" target="_blank"><i className="fa fa-github fa-2x"></i><br/><span>GitHub</span></a>
        </div>
      </div>
    </>
  );
};

export default Header;